import React, { FC, useRef, useState, useEffect } from "react";
import { Coordinates, TeamType } from "types";
import { ToastNotificationEnum } from "components/ToastNotification/enum";
import Prompt from "components/Prompt/Prompt";
import ResetPositions from "components/ResetPositions/ResetPositions";
import Divider from "components/common/Divider/Divider";
import { putTimer, deleteTimer } from "api/TimerApi";
import Timer from "components/Timer/Timer";
import TimerSettingModal from "./TimerSettingModal";
import Button from "components/common/Button/Button";
import { handleError } from "api/utils";
import styles from "./Header.module.scss";

interface IHeaderProps {
  room: TeamType;
  prompt: string;
  showToast: (message: string, type: ToastNotificationEnum) => void;
  membersPositions: Map<string, Coordinates>;
  timeLeft: number | null;
}

export const Header: FC<IHeaderProps> = (props) => {
  const headerRef = useRef<HTMLElement>(null);
  const [isTimerSettingModalOpen, setIsTimerSettingModalOpen] = useState(false);
  const [isSetTimerButtonVisible, setIsSetTimerButtonVisible] = useState(
    !props.timeLeft
  );

  const onSetTimerClick = () => {
    setIsTimerSettingModalOpen(true);
  };

  const setTimer = async (timer: number) => {
    try {
      await putTimer({ roomId: props.room.id, timer });
      props.showToast("Timer set!", ToastNotificationEnum.SUCCESS);
    } catch (error: unknown) {
      handleError({
        error,
        showErrorToast: (message: string) =>
          props.showToast(message, ToastNotificationEnum.FAILURE),
        defaultErrorMessage: "An error occurred while setting the timer",
      });
    }
  };

  const clearTimer = async () => {
    try {
      console.log("Clearing timer...");
      await deleteTimer({ roomId: props.room.id });
      props.showToast("Timer cleared!", ToastNotificationEnum.SUCCESS);
    } catch (error: unknown) {
      handleError({
        error,
        showErrorToast: (message: string) =>
          props.showToast(message, ToastNotificationEnum.FAILURE),
        defaultErrorMessage: "An error occurred while clearing the timer",
      });
    }
  };

  // Sync internal state with props, needed when props change outside of component (ex: after GET room)
  useEffect(() => {
    setIsSetTimerButtonVisible(!props.timeLeft);
  }, [props.timeLeft, setIsSetTimerButtonVisible]);

  return (
    <header className={styles.header} ref={headerRef}>
      <div className={styles["tag-line"]}>
        <div className={`${styles["tag-line-decoration"]} ${styles["left"]}`} />
        <h4>
          <i>{props.room.name} Sprint Retro</i>
        </h4>
        <div
          className={`${styles["tag-line-decoration"]} ${styles["right"]}`}
        />
      </div>
      <div className={styles["prompt-and-buttons-container"]}>
        <Prompt
          roomId={props.room.id}
          prompt={props.prompt}
          showToast={props.showToast}
        />
        <ResetPositions
          roomId={props.room.id}
          membersPositions={props.membersPositions}
          showToast={props.showToast}
        />
        {isSetTimerButtonVisible && (
          <Button onClick={onSetTimerClick} text="Set timer" />
        )}
        <Timer
          timeLeft={props.timeLeft}
          setIsSetTimerButtonVisible={setIsSetTimerButtonVisible}
          clearTimer={clearTimer}
        />
      </div>
      <Divider />
      <TimerSettingModal
        isOpen={isTimerSettingModalOpen}
        setIsOpen={() => setIsTimerSettingModalOpen(false)}
        setTimer={setTimer}
      />
    </header>
  );
};
