import React, { Dispatch, FC, SetStateAction, useState, useRef } from "react";
import ModalDialogWithLayout from "components/common/ModalDialogWithLayout/ModalDialogWithLayout";
import Button from "components/common/Button/Button";
import styles from "./TimerSettingModal.module.scss";

interface ITimerSettingModalProps {
  isOpen: boolean;
  setIsOpen: Dispatch<SetStateAction<boolean>>;
  setTimer: (time: number) => void;
}

const TimerSettingModal: FC<ITimerSettingModalProps> = (props) => {
  const secondsRef = useRef<HTMLInputElement>(null);
  const [minutes, setMinutes] = useState<number | null>(null);
  const [seconds, setSeconds] = useState<number | null>(null);

  const saveTimer = () => {
    if (minutes == null && seconds == null) {
      console.log("Timer setting: invalid input min", minutes, "sec", seconds);
      return;
    }

    const minutesValue = minutes ?? 0;
    const secondsValue = seconds ?? 0;
    props.setTimer(minutesValue * 60 + secondsValue);
    props.setIsOpen(false);
  };

  const onKeyPressMinutes = (e: React.KeyboardEvent<HTMLInputElement>) => {
    if (e.key === "Enter") {
      if (secondsRef.current) {
        secondsRef.current.focus();
      }
    }
  };

  const onKeyPressSeconds = (e: React.KeyboardEvent<HTMLInputElement>) => {
    if (e.key === "Enter") {
      saveTimer();
    }
  };

  return (
    <ModalDialogWithLayout
      isOpen={props.isOpen}
      setIsOpen={props.setIsOpen}
      title="Set timer"
      actionButtons={<Button onClick={saveTimer} text="Save" />}
    >
      <div className={styles["timer-setting"]}>
        <input
          value={minutes ?? ""}
          onChange={(e) => setMinutes(+e.target.value)}
          className={styles["timer-setting-input"]}
          placeholder="00"
          onKeyPress={onKeyPressMinutes}
        />
        :
        <input
          value={seconds ?? ""}
          onChange={(e) => setSeconds(+e.target.value)}
          className={styles["timer-setting-input"]}
          placeholder="00"
          onKeyPress={onKeyPressSeconds}
          ref={secondsRef}
        />
      </div>
    </ModalDialogWithLayout>
  );
};

export default TimerSettingModal;
