import React, { Dispatch, FC, SetStateAction } from "react";
import WarningAlert from "components/common/WarningAlert/WarningAlert";

interface ITimerDeleteModalProps {
  isOpen: boolean;
  setIsOpen: Dispatch<SetStateAction<boolean>>;
  deleteTimer: () => Promise<void>;
}

const TimerDeleteModal: FC<ITimerDeleteModalProps> = ({
  isOpen,
  setIsOpen,
  deleteTimer,
}: ITimerDeleteModalProps) => {
  return (
    <WarningAlert isOpen={isOpen} setIsOpen={setIsOpen} onConfirm={deleteTimer}>
      You are about to clear the timer for all players in the room.
      <br></br>
      <br></br>
      Are you sure you want to proceed ?
    </WarningAlert>
  );
};

export default TimerDeleteModal;
