import React, { ReactNode } from "react";
import ModalDialog, {
  ModalDialogPropsType,
} from "components/common/ModalDialog/ModalDialog";
import CloseIcon from "components/common/Icons/CloseIcon";
import styles from "./ModalDialogWithLayout.module.scss";

type ModalDialogWithLayoutPropsType = ModalDialogPropsType & {
  title: string;
  subtitle?: string;
  actionButtons?: ReactNode;
};

export default function ModalDialogWithLayout({
  isOpen,
  setIsOpen,
  title,
  children,
  closeOnClickOutside = true,
  className = "",
  subtitle = "",
  actionButtons,
}: ModalDialogWithLayoutPropsType) {
  return (
    <ModalDialog
      isOpen={isOpen}
      setIsOpen={setIsOpen}
      className={className}
      closeOnClickOutside={closeOnClickOutside}
      withLayout={true}
    >
      <div className={styles["modal-dialog-with-layout-header"]}>
        <div
          className={styles["modal-dialog-with-layout-header-title-container"]}
        >
          <h2>{title}</h2>
          <button onClick={() => setIsOpen(false)}>
            <CloseIcon color="#f25c54" /> {/* --color-primary-5 */}
          </button>
        </div>
        <h3>{subtitle}</h3>
      </div>
      <div className={styles["modal-dialog-with-layout-children-container"]}>
        {children}
      </div>
      {actionButtons && (
        <div
          className={
            styles["modal-dialog-with-layout-action-buttons-container"]
          }
        >
          {actionButtons}
        </div>
      )}
    </ModalDialog>
  );
}
