import React from "react";
import { HashRouter, Route, Routes } from "react-router-dom";
import { formatName } from "ts/utils";
import { TeamType } from "types";
import { Footer } from "./components/Footer/Footer";
import Home from "./components/Home/Home";
import Room from "components/Room/Room";
import { WEBCX_TEAM } from "./data/webcx";
import { MA_TEAM } from "data/ma";
import "./App.scss";

function App() {
  const rooms: TeamType[] = [WEBCX_TEAM, MA_TEAM];

  return (
    <div className={`app-container`}>
      <HashRouter>
        <Routes>
          {rooms.map((room) => (
            <Route
              path={`/${formatName(room.name)}`}
              element={<Room room={room} />}
              key={room.id}
            ></Route>
          ))}

          <Route path="/" element={<Home teams={rooms} />}></Route>
        </Routes>
        <Footer />
      </HashRouter>
    </div>
  );
}

export default App;
