import React, { FC, useRef, useState, useEffect, ChangeEvent } from "react";
import { putPrompt } from "api/PromptApi";
import { handleError } from "api/utils";
import { ToastNotificationEnum } from "components/ToastNotification/enum";
import PublishPromptAlert from "components/Header/PublishPromptAlert";
import Button from "components/common/Button/Button";
import styles from "./Prompt.module.scss";

interface IPromptProps {
  roomId: string;
  prompt: string;
  showToast: (message: string, type: ToastNotificationEnum) => void;
}

const Prompt: FC<IPromptProps> = (props) => {
  const promptTextAreaRef = useRef<HTMLTextAreaElement>(null);
  const promptTextAreaHiddenRef = useRef<HTMLTextAreaElement>(null);
  const [promptHeight, setPromptHeight] = useState(0);
  const [prompt, setPrompt] = useState(props.prompt);
  const [isPublishPromptAlertOpen, setIsPublishPromptAlertOpen] =
    useState(false);

  const onPublishClick = () => {
    setIsPublishPromptAlertOpen(true);
  };

  const publishPrompt = async () => {
    try {
      await putPrompt({ roomId: props.roomId, prompt });
      props.showToast("Prompt updated!", ToastNotificationEnum.SUCCESS);
    } catch (error: unknown) {
      handleError({
        error,
        showErrorToast: (message: string) =>
          props.showToast(message, ToastNotificationEnum.FAILURE),
        defaultErrorMessage: "An error occurred while updating the prompt",
      });
    }
  };

  const updatePromptHeight = () => {
    if (promptTextAreaRef.current && promptTextAreaHiddenRef.current) {
      setPromptHeight(promptTextAreaHiddenRef.current.scrollHeight);
    }
  };

  const onInputChange = (e: ChangeEvent<HTMLTextAreaElement>) => {
    setPrompt(e.target.value);
  };

  // Sync internal state with props, needed when props change outside of component (ex: after GET room)
  useEffect(() => {
    setPrompt(props.prompt);
  }, [props.prompt, setPrompt]);

  useEffect(() => {
    updatePromptHeight();
  }, [prompt]);

  return (
    <>
      <div className={styles.prompt}>
        <textarea
          value={prompt}
          onChange={onInputChange}
          ref={promptTextAreaRef}
          style={{ height: promptHeight }}
        />
        <textarea
          className={`${styles.prompt} ${styles["hidden-textarea"]}`}
          defaultValue={prompt}
          rows={1}
          ref={promptTextAreaHiddenRef}
          style={{ width: promptTextAreaRef.current?.clientWidth }}
        />
        <Button onClick={onPublishClick} text="Publish" />
      </div>
      <PublishPromptAlert
        isOpen={isPublishPromptAlertOpen}
        setIsOpen={setIsPublishPromptAlertOpen}
        publishPrompt={publishPrompt}
      />
    </>
  );
};

export default Prompt;
