import React, { FC, useEffect, useState } from "react";
import { v4 as uuidv4 } from "uuid";
import Arena from "components/Arena/Arena";
import TeamMember from "components/TeamMember/TeamMember";
import { Header } from "components/Header/Header";
import { getRoom } from "api/RoomApi";
import { useToastNotifications } from "hooks/useToastNotifications";
import ToastList from "components/ToastNotification/List/ToastList";
import { TeamType, PositionFromBackend, Coordinates } from "types";
import { POSITION } from "components/ToastNotification/constants";
import {
  BASE_URL,
  PROMPT_EVENT,
  POSITION_EVENT,
  TIMER_EVENT,
} from "api/constants";
import { handleErrorConsoleLog } from "api/utils";
import { calculateTimeLeft } from "ts/timerUtils";
import styles from "./Room.module.scss";

interface IRoomProps {
  room: TeamType;
}

const Room: FC<IRoomProps> = (props) => {
  const [prompt, setPrompt] = useState("");
  const [membersPositions, setMembersPositions] = useState<
    Map<string, Coordinates>
  >(new Map<string, Coordinates>());
  const [timerEnd, setTimerEnd] = useState<number | null>(null);
  const timeLeft = calculateTimeLeft(timerEnd);

  const { toasts, removeToast, showToast } = useToastNotifications();

  const updateMap = ({
    id,
    position,
  }: {
    id: string;
    position: Coordinates;
  }) => {
    if (position) {
      setMembersPositions((prev) => {
        const clone = new Map(prev);
        clone.set(id, position);
        return clone;
      });
    } else if (id && !position) {
      setMembersPositions((prev) => {
        const clone = new Map(prev);
        clone.delete(id);
        return clone;
      });
    }
  };

  useEffect(() => {
    const asyncFunc = async () => {
      try {
        const { prompt, members, timerEnd } = await getRoom(props.room.id);
        setPrompt(prompt);
        members.forEach(updateMap);
        setTimerEnd(timerEnd);
      } catch (error: unknown) {
        handleErrorConsoleLog({
          error,
          defaultErrorMessage: "Error when GET room.",
        });
      }
    };
    asyncFunc();
    // eslint-disable-next-line react-hooks/exhaustive-deps
  }, [props.room.id]);

  useEffect(() => {
    const eventSource = new EventSource(
      `${BASE_URL}/rooms/${props.room.id}/events`
    );

    // Add event listener for prompt update
    const onPromptUpdate = (event: MessageEvent) => {
      const prompt = JSON.parse(event.data).prompt;
      setPrompt(prompt);
    };
    eventSource.addEventListener(PROMPT_EVENT, onPromptUpdate);

    // Add event listener for positions update
    const onPositionUpdate = (event: MessageEvent) => {
      const positionsFromBackEnd: PositionFromBackend[] = JSON.parse(
        event.data
      ).members;

      positionsFromBackEnd.forEach(updateMap);
    };
    eventSource.addEventListener(POSITION_EVENT, onPositionUpdate);

    // Add event listener for timer update
    const onTimerUpdate = (event: MessageEvent) => {
      const timerEnd = JSON.parse(event.data).timerEnd;
      setTimerEnd(timerEnd);
    };
    eventSource.addEventListener(TIMER_EVENT, onTimerUpdate);

    return () => {
      eventSource.removeEventListener(PROMPT_EVENT, onPromptUpdate);
      eventSource.removeEventListener(POSITION_EVENT, onPositionUpdate);
      eventSource.removeEventListener(TIMER_EVENT, onTimerUpdate);
      eventSource.close();
    };
  }, [props.room.id]);

  return (
    <>
      <Header
        room={props.room}
        prompt={prompt}
        showToast={showToast}
        membersPositions={membersPositions}
        timeLeft={timeLeft}
      />
      <div className={styles.room}>
        <Arena
          room={props.room}
          showToast={showToast}
          membersPositions={membersPositions}
          setMembersPositions={setMembersPositions}
        />
        <div className={styles["room-members-container"]}>
          {props.room.members.map((line) => (
            <div className={styles["room-members-line"]} key={uuidv4()}>
              {line.map((member) => (
                <TeamMember key={member.name} teamMember={member} />
              ))}
            </div>
          ))}
        </div>
      </div>

      <ToastList data={toasts} position={POSITION} removeToast={removeToast} />
    </>
  );
};

export default Room;
