import WarningAlert from "components/common/WarningAlert/WarningAlert";
import React, { Dispatch, SetStateAction } from "react";

interface IResetPositionsAlertProps {
  isOpen: boolean;
  setIsOpen: Dispatch<SetStateAction<boolean>>;
  resetPositions: () => Promise<void>;
}

const ResetPositionsAlert: React.FC<IResetPositionsAlertProps> = ({
  isOpen,
  setIsOpen,
  resetPositions,
}) => {
  return (
    <WarningAlert
      isOpen={isOpen}
      setIsOpen={setIsOpen}
      onConfirm={resetPositions}
    >
      You are about to reset the positions of all members in the room.
      <br></br>
      <br></br>
      Are you sure you want to proceed ?
    </WarningAlert>
  );
};

export default ResetPositionsAlert;
