export const calculateTimeLeft = (timerEnd: number | null) => {
  if (timerEnd === null) {
    return null;
  }

  const now = new Date().getTime();
  const diff = Math.floor((timerEnd * 1000 - now) / 1000);

  if (diff < 0) {
    console.log("timer in the past");
    return null;
  }

  return diff;
};
