import React, {
  FC,
  useState,
  useEffect,
  Dispatch,
  SetStateAction,
} from "react";
import { FontAwesomeIcon } from "@fortawesome/react-fontawesome";
import { faHourglass } from "@fortawesome/free-solid-svg-icons";
import CloseIcon from "components/common/Icons/CloseIcon";
import TimerDeleteModal from "./TimerDeleteModal";
import styles from "./Timer.module.scss";

interface ITimerProps {
  timeLeft: number | null; // in seconds
  setIsSetTimerButtonVisible: Dispatch<SetStateAction<boolean>>;
  clearTimer: () => Promise<void>;
}

const Timer: FC<ITimerProps> = (props) => {
  const [timeLeft, setTimeLeft] = useState<number | null>(props.timeLeft);
  const [isExiting, setIsExiting] = useState<boolean>(false);
  const [isTimerDeleteOpen, setIsTimerDeleteOpen] = useState<boolean>(false);

  const minutes = timeLeft ? Math.floor(timeLeft / 60) : 0;
  const seconds = timeLeft ? timeLeft % 60 : 0;

  const isExistingClassName = isExiting ? styles.exiting : "";
  const shouldDisplayTimer = timeLeft !== null && timeLeft >= 0;

  useEffect(() => {
    if (timeLeft === 0) {
      setIsExiting(true);
    }

    if (timeLeft === -1 || timeLeft == null) {
      props.setIsSetTimerButtonVisible(true);
      setIsExiting(false);
      return;
    }

    const intervalId = setInterval(() => {
      setTimeLeft(timeLeft - 1);
    }, 1000);

    return () => clearInterval(intervalId);
    // eslint-disable-next-line react-hooks/exhaustive-deps
  }, [timeLeft]);

  // Sync internal state with props, needed when props change outside of component (ex: after GET room)
  useEffect(() => {
    setTimeLeft(props.timeLeft);
  }, [props.timeLeft, setTimeLeft]);

  return (
    <>
      {shouldDisplayTimer && (
        <div className={`${styles.timer} ${isExistingClassName}`}>
          <FontAwesomeIcon icon={faHourglass} />
          <span className={styles["timer-time"]}>
            {minutes.toString().padStart(2, "0")}:
            {seconds.toString().padStart(2, "0")}
          </span>
          <button onClick={() => setIsTimerDeleteOpen(true)}>
            <CloseIcon color="#f25c54" /> {/* --color-primary-5 */}
          </button>
        </div>
      )}
      <TimerDeleteModal
        isOpen={isTimerDeleteOpen}
        setIsOpen={setIsTimerDeleteOpen}
        deleteTimer={props.clearTimer}
      />
    </>
  );
};

export default Timer;
