import WarningAlert from "components/common/WarningAlert/WarningAlert";
import React, { FC, Dispatch, SetStateAction } from "react";

interface IPublishPromptAlertProps {
  isOpen: boolean;
  setIsOpen: Dispatch<SetStateAction<boolean>>;
  publishPrompt: () => Promise<void>;
}

const PublishPromptAlert: FC<IPublishPromptAlertProps> = ({
  isOpen,
  setIsOpen,
  publishPrompt,
}) => {
  return (
    <WarningAlert
      isOpen={isOpen}
      setIsOpen={setIsOpen}
      onConfirm={publishPrompt}
    >
      You are about to update the prompt for all members in the room.
      <br></br>
      <br></br>
      Are you sure you want to proceed ?
    </WarningAlert>
  );
};

export default PublishPromptAlert;
