import React, { FC, useState } from "react";
import { Coordinates } from "types";
import { ToastNotificationEnum } from "components/ToastNotification/enum";
import { handleError } from "api/utils";
import { putPosition } from "api/PositioningApi";
import { isIterable } from "ts/utils";
import ResetPositionsAlert from "components/Header/ResetPositionsAlert";
import Button from "components/common/Button/Button";

interface IResetPositionsProps {
  roomId: string;
  membersPositions: Map<string, Coordinates>;
  showToast: (message: string, type: ToastNotificationEnum) => void;
}

const ResetPositions: FC<IResetPositionsProps> = (props) => {
  const [isResetPositionsAlertOpen, setIsResetPositionsAlertOpen] =
    useState(false);

  const onResetClick = () => {
    setIsResetPositionsAlertOpen(true);
  };

  const resetPositions = async () => {
    try {
      Array.from(props.membersPositions).forEach((memberPosition, index) => {
        if (!isIterable(memberPosition)) return null;
        const [memberId] = memberPosition;
        setTimeout(async () => {
          await putPosition({
            roomId: props.roomId,
            memberId,
            coordinates: { x: null, y: null },
          });
        }, index * 50);
      });
    } catch (error: unknown) {
      handleError({
        error,
        showErrorToast: (message: string) =>
          props.showToast(message, ToastNotificationEnum.FAILURE),
        defaultErrorMessage: "An error occurred while resetting all positions.",
      });
    }
  };

  return (
    <>
      <Button
        onClick={onResetClick}
        text="Reset"
        disabled={props.membersPositions.size === 0}
      />
      <ResetPositionsAlert
        isOpen={isResetPositionsAlertOpen}
        setIsOpen={setIsResetPositionsAlertOpen}
        resetPositions={resetPositions}
      />
    </>
  );
};

export default ResetPositions;
